import React from 'react'
import { graphql } from 'gatsby'

import Box from '@material-ui/core/Box'

// Components
import SiteHeader from '../components/Headers/SiteHeader'
import GridList from '../components/Lists/GridList'
import FluidContainerList from "../components/Lists/FluidContainerList"
import Toolbar from "@material-ui/core/Toolbar"
import ScrollTop from "../components/ScrollTop/ScrollTop"
import Fab from "@material-ui/core/Fab"
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp"
import ArticlesTagsToolbar from '../components/LandingPage/ArticlesTagsToolbar'
import Divider from '@material-ui/core/Divider'
import useSiteMetaData from '../data/useSiteMetaData'
import Footer from '../components/Footer/Footer'
import ArticleHeader from '../components/Headers/ArticleHeader'

/**
 * ArticlesTagsTemplate template
 * @param pageContext
 * @param data
 * @param location
 * @param navigate
 * @returns {*}
 * @constructor
 */
function ArticlesTagsTemplate({ pageContext, data, location, navigate }) {
  const siteMeta = useSiteMetaData()
  const { tag } = pageContext
  const { edges } = data.allMarkdownRemark
  //const { edges, totalCount } = data.allMarkdownRemark
  //const description = `${totalCount} post${totalCount === 1 ? '' : 's'} tagged with "${tag}"`

  return (
    <React.Fragment>
      <SiteHeader
        title={'Articles about ' + tag}
        description={`Articles about ${tag}`}
        location={location}
        navigate={navigate}
        siteMeta={siteMeta}
      />
      <Toolbar id="back-to-top-anchor" />
      <Box pt={8} pr={1} pb={2} pl={1}>
        <ArticlesTagsToolbar />
        <Box pt={1} pb={1}>
          <Divider variant="fullWidth" />
        </Box>
        <FluidContainerList edges={edges} location={location} />
      </Box>
      <Footer siteMeta={siteMeta}/>
      <ScrollTop>
        <Fab color="secondary" aria-label="scroll back to top">
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment>
  )
}

export const query = graphql`
  query tagQuery($tag: String) {
    allMarkdownRemark(
      filter: {
        fileAbsolutePath: { regex: "/(articles)/.*\\\\.md$/" }
        frontmatter: { tags: { in: [$tag] }, published: { ne: "No" } }
      }
      sort: { fields: [frontmatter___date], order: DESC }
      limit: 400
    ) {
      totalCount
      edges {
        node {
          excerpt(pruneLength: 160)
          timeToRead
          fields {
            slug
          }
          frontmatter {
            title
            subtitle
            date(formatString: "YYYY-MM-DD")
            publishedBy
            image {
              publicURL
            }
          }
        }
      }
    }
  }
`

export default ArticlesTagsTemplate
